import React, { useState } from 'react';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  Cell,
} from 'recharts';
import { Book, Award, TrendingUp, RefreshCw } from 'lucide-react';
import { motion } from 'framer-motion';
import CategoryWordsPopup from './CategoryWordsPopup';

const Dashboard = ({
  words,
  categorizedWords,
  addToCategory,
  removeFromCategory,
  darkMode,
}) => {
  const [selectedCategory, setSelectedCategory] = useState(null);

  const totalWords = words.length;
  const learnedWords = categorizedWords['Öğrendiğim Kelimeler']?.length || 0;
  const difficultWords = categorizedWords['Zorlandığım Kelimeler']?.length || 0;
  const reviewWords =
    categorizedWords['Tekrar Edilecek Kelimeler']?.length || 0;
  const remainingWords =
    totalWords - learnedWords - difficultWords - reviewWords;

  const data = [
    { name: 'Öğrendiğim', value: learnedWords, color: '#10B981' },
    { name: 'Zorlandığım', value: difficultWords, color: '#EF4444' },
    { name: 'Tekrar Edilecek', value: reviewWords, color: '#F59E0B' },
    { name: 'Kalan', value: remainingWords, color: '#3B82F6' },
  ];

  const StatCard = ({ icon: Icon, title, value, color, onClick }) => (
    <motion.div
      whileHover={{ scale: 1.05, boxShadow: '0px 0px 15px rgba(0,0,0,0.2)' }}
      className={`${
        darkMode ? 'bg-gray-800' : 'bg-white'
      } p-6 rounded-lg flex items-center cursor-pointer transition-transform duration-300`}
      onClick={onClick}
    >
      <div
        className="p-4 rounded-full mr-4"
        style={{ backgroundColor: `${color}1A` }}
      >
        <Icon color={color} size={36} />
      </div>
      <div>
        <p
          className={`text-sm ${
            darkMode ? 'text-gray-400' : 'text-gray-600'
          }`}
        >
          {title}
        </p>
        <p className="text-3xl font-bold" style={{ color: color }}>
          {value}
        </p>
      </div>
    </motion.div>
  );

  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      return (
        <div
          className={`p-4 ${
            darkMode ? 'bg-gray-800 text-white' : 'bg-white text-gray-800'
          } rounded shadow-md`}
        >
          <p className="font-bold">{`${label}: ${payload[0].value}`}</p>
        </div>
      );
    }
    return null;
  };

  // Animasyon varyantları
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: { opacity: 1 },
  };

  return (
    <motion.div
      initial="hidden"
      animate="visible"
      variants={containerVariants}
      transition={{ duration: 0.5 }}
      className={`${
        darkMode ? 'bg-gray-900' : 'bg-gray-100'
      } rounded-lg shadow-md p-6`}
    >
      <h2
        className={`text-3xl font-bold mb-6 ${
          darkMode ? 'text-white' : 'text-gray-800'
        }`}
      >
        Dashboard
      </h2>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        <StatCard
          icon={Book}
          title="Toplam Kelime"
          value={totalWords}
          color="#3B82F6"
        />
        <StatCard
          icon={Award}
          title="Öğrendiğim Kelimeler"
          value={learnedWords}
          color="#10B981"
          onClick={() => setSelectedCategory('Öğrendiğim Kelimeler')}
        />
        <StatCard
          icon={TrendingUp}
          title="Zorlandığım Kelimeler"
          value={difficultWords}
          color="#EF4444"
          onClick={() => setSelectedCategory('Zorlandığım Kelimeler')}
        />
        <StatCard
          icon={RefreshCw}
          title="Tekrar Edilecek Kelimeler"
          value={reviewWords}
          color="#F59E0B"
          onClick={() => setSelectedCategory('Tekrar Edilecek Kelimeler')}
        />
      </div>

      <div
        className={`${
          darkMode ? 'bg-gray-800' : 'bg-white'
        } rounded-lg shadow-md p-6 mb-8`}
      >
        <h3
          className={`text-xl font-bold mb-4 ${
            darkMode ? 'text-white' : 'text-gray-800'
          }`}
        >
          Kelime Dağılımı
        </h3>
        <div className="h-80">
          <ResponsiveContainer width="100%" height="100%">
            <BarChart
              data={data}
              margin={{
                top: 20,
                right: 30,
                left: 20,
                bottom: 5,
              }}
            >
              <CartesianGrid
                strokeDasharray="3 3"
                stroke={darkMode ? '#374151' : '#E5E7EB'}
              />
              <XAxis
                dataKey="name"
                stroke={darkMode ? '#D1D5DB' : '#4B5563'}
                tick={{ fill: darkMode ? '#D1D5DB' : '#4B5563' }}
              />
              <YAxis
                stroke={darkMode ? '#D1D5DB' : '#4B5563'}
                tick={{ fill: darkMode ? '#D1D5DB' : '#4B5563' }}
              />
              <Tooltip content={<CustomTooltip />} />
              <Bar dataKey="value" radius={[10, 10, 0, 0]}>
                {data.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.color} />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>

      {selectedCategory && (
        <CategoryWordsPopup
          category={selectedCategory}
          words={categorizedWords[selectedCategory] || []}
          onClose={() => setSelectedCategory(null)}
          addToCategory={addToCategory}
          removeFromCategory={removeFromCategory}
          darkMode={darkMode}
        />
      )}
    </motion.div>
  );
};

export default Dashboard;
