import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {
  Settings as SettingsIcon,
  Moon,
  Sun,
  LogOut,
  Shield,
  User,
} from 'lucide-react';

const ProfileMenu = ({
  isMenuOpen,
  toggleMenu,
  toggleDarkMode,
  darkMode,
  setActiveTab,
  handleLogout,
  userSettings,
}) => {
  return (
    <>
      {/* Arka Plan Overlay */}
      <AnimatePresence>
        {isMenuOpen && (
          <>
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 0.3 }}
              exit={{ opacity: 0 }}
              transition={{ duration: 0.2 }}
              className="fixed inset-0 bg-black dark:bg-gray-900 z-40"
              onClick={toggleMenu}
            />

            {/* Menü Kendisi */}
            <motion.div
              initial={{ opacity: 0, scale: 0.95, y: -20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.95, y: -20 }}
              transition={{ duration: 0.2 }}
              className="absolute right-4 top-16 w-60 bg-white dark:bg-gray-800 rounded-lg shadow-lg z-50"
            >
              <div className="p-4 border-b border-gray-200 dark:border-gray-700">
                <div className="flex items-center space-x-3">
                  <img
                    src={userSettings.profileImage || 'default-avatar.png'}
                    alt="Profil"
                    className="w-10 h-10 rounded-full object-cover border-2 border-gray-300 dark:border-gray-600 shadow-sm"
                  />
                  <div>
                    <p className="text-sm font-semibold text-gray-900 dark:text-white">
                      {userSettings.username || 'Kullanıcı'}
                    </p>
                    <p className="text-xs text-gray-500 dark:text-gray-400">
                      {userSettings.email || 'email@example.com'}
                    </p>
                  </div>
                </div>
              </div>
              <div className="py-2">
                {/* Dark Mode Toggle */}
                <motion.button
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  onClick={() => {
                    toggleDarkMode();
                    toggleMenu();
                  }}
                  className="flex items-center w-full px-4 py-2 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-700 focus:outline-none"
                >
                  {darkMode ? (
                    <Sun className="mr-2" size={20} />
                  ) : (
                    <Moon className="mr-2" size={20} />
                  )}
                  {darkMode ? 'Gündüz Modu' : 'Karanlık Mod'}
                </motion.button>

                {/* Ayarlar */}
                <motion.button
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  onClick={() => {
                    setActiveTab('settings');
                    toggleMenu();
                  }}
                  className="flex items-center w-full px-4 py-2 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-700 focus:outline-none"
                >
                  <SettingsIcon className="mr-2" size={20} />
                  Ayarlar
                </motion.button>

                {/* Profil */}
                <motion.button
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  onClick={() => {
                    setActiveTab('profile');
                    toggleMenu();
                  }}
                  className="flex items-center w-full px-4 py-2 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-700 focus:outline-none"
                >
                  <User className="mr-2" size={20} />
                  Profil
                </motion.button>

                {/* Admin Paneli (Sadece Adminler İçin) */}
                {userSettings.isAdmin && (
                  <motion.button
                    whileHover={{ scale: 1.02 }}
                    whileTap={{ scale: 0.98 }}
                    onClick={() => {
                      setActiveTab('admin');
                      toggleMenu();
                    }}
                    className="flex items-center w-full px-4 py-2 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-700 focus:outline-none"
                  >
                    <Shield className="mr-2" size={20} />
                    Admin Paneli
                  </motion.button>
                )}

                {/* Çıkış Yap */}
                <motion.button
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  onClick={() => {
                    handleLogout();
                    toggleMenu();
                  }}
                  className="flex items-center w-full px-4 py-2 text-sm text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-700 focus:outline-none"
                >
                  <LogOut className="mr-2" size={20} />
                  Çıkış Yap
                </motion.button>
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </>
  );
};

export default ProfileMenu;
